<template>
  <NoItem :text="$t('favorite.title')" v-if="!teachers?.length">
    <font-awesome-icon :icon="plusIcon" />
  </NoItem>
  <div class="wrap" v-else>
    <div class="item-wrap">
      <article class="teacher-item" v-for="(teacher, index) in teachers" :key="index">
        <router-link :to="{ name: 'TeacherDetail', params: { id: teacher.Instructor.id } }">
          <img :src="teacher.Instructor.icon" alt="" class="image" />
          <div class="teacher-info">
            <span class="teacher-name">{{ teacher.Instructor.name }}</span>
          </div>
        </router-link>
        <button class="delete-button" @click="deleteFavorite(teacher.Instructor.id)">
          <font-awesome-icon :icon="closeIcon" class="close-icon" />
        </button>
      </article>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, computed } from 'vue'
import { useMutation, useQuery, useResult } from '@vue/apollo-composable'
import favoriteInstructors from '@/graphql/queries/favoriteInstructors'
import { DeleteFavoriteInstructorMutation, FavoriteInstructors } from '@/graphql/@types/types'
import { useStore } from 'vuex'
import { faTimes, faPlus } from '@fortawesome/free-solid-svg-icons'
import deleteFavoriteInstructorMutation from '@/graphql/mutations/deleteFavoriteInstructor'
import NoItem from '@/components/NoItem.vue'

export default defineComponent({
  components: {
    NoItem
  },
  setup() {
    const store = useStore()
    const uid = store.state.currentUser?.uid
    const { result, refetch } = useQuery<FavoriteInstructors>(favoriteInstructors, {
      student_id: uid
    })
    refetch()

    const { mutate: deleteFavoriteInstructor } = useMutation<DeleteFavoriteInstructorMutation>(deleteFavoriteInstructorMutation, () => ({
      update: () => {
        refetch()
      }
    }))
    const deleteFavorite = async (instructorId: number) => {
      await deleteFavoriteInstructor({
        instructor_id: instructorId,
        student_id: uid
      })
    }
    const teachers = useResult(result)
    const closeIcon = computed(() => faTimes)
    const plusIcon = computed(() => faPlus)

    return {
      closeIcon,
      plusIcon,
      teachers,
      deleteFavorite
    }
  }
})
</script>

