import { gql } from '@apollo/client/core'

const favoriteInstructors = gql`
  query FavoriteInstructors($student_id: uuid) {
    FavoriteInstructors(where: { student_id: { _eq: $student_id } }) {
      Instructor {
        id
        icon
        name
      }
    }
  }
`

export default favoriteInstructors
